:root {
  --color-primary-dark100: #253000;
  --color-primary-dark90: #324000;
  --color-primary-dark80: #485c00;
  --color-primary-dark70: #729100;
  --color-primary-light100: #c8ff00;
  --color-primary-light50: #e3ff7b;
  --color-grey-100: #030404;
  --color-grey-90: #101010;
  --color-grey-85: #101515;
  --color-grey-80: #1b1c19;
  --color-grey-70: #31322f;
  --color-grey-60: #3b4853;
  --color-grey-50: #727e88;
  --color-grey-30: #a3b1bc;
  --color-grey-10: #e8ecf2;
  --color-grey-0: #fff;
  --color-system-error: #cd3e13;
  --color-system-blue: #09a0c1;
  --color-system-green: #008625;
  --font-primary: "IBM Plex Sans", Arial, sans-serif;
  --font-secondary: "Tektur", sans-serif;
  --max-width: 1440px;
  --medium-width-2: 1024px;
  --medium-width: 768px;
  --small-width: 393px;
  --min-header-height: 60px;
  --mobile-header-height: 72px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: var(--font-primary);
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.4;
  position: relative;
}

@media (width <= 767px) {
  html {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }
}

body {
  color: var(--color-grey-10);
  background-color: var(--color-grey-85);
  width: 100%;
  height: 100%;
  position: relative;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

address {
  font-style: normal;
}

img, picture, video, iframe, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  font: inherit;
  background: none;
  border: none;
  padding: 0;
}

input, textarea, select {
  font: inherit;
  color: inherit;
  box-shadow: none;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

select {
  appearance: none;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  text-align: center;
  cursor: pointer;
  background-color: var(--color-primary-dark80);
  color: var(--color-grey-0);
  padding: 18px 46px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  transition: background-color .3s;
  display: inline-block;
}

@media (width <= 767px) {
  .button {
    width: 100%;
    padding: 18px 32px;
    font-size: 24px;
  }
}

.button:hover {
  background-color: var(--color-primary-dark90);
  transition: background-color .3s;
}

.button:focus {
  background-color: var(--color-primary-dark100);
}

.button--small {
  padding: 8px 24px;
  font-size: 16px;
}

.button--large {
  padding: 16px 52px;
  font-size: 20px;
}

@media (width <= 767px) {
  .button--large {
    padding: 24px 52px;
    font-size: 24px;
  }
}

.main-container {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 112px;
}

@media (width <= 767px) {
  .container {
    max-width: 393px;
    padding: 0 24px;
  }
}

main {
  flex: 1;
}

.section-title {
  font-family: var(--font-primary);
  letter-spacing: 2px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--color-primary-dark70);
  font-size: 40px;
  font-style: normal;
  font-weight: 600;
  line-height: 110%;
}

@media (width <= 767px) {
  .section-title {
    letter-spacing: 1.2px;
    font-size: 24px;
  }
}

.section-title--white {
  font-family: var(--font-secondary);
  letter-spacing: 3.36px;
  color: var(--color-grey-10);
  font-size: 56px;
  font-style: normal;
  font-weight: 500;
  line-height: 92%;
}

@media (width <= 767px) {
  .section-title--white {
    letter-spacing: .2px;
    font-size: 40px;
    font-weight: 500;
    line-height: 100%;
  }
}

.header {
  background-color: var(--color-grey-85);
  z-index: 1000;
  width: 100%;
  min-height: var(--min-header-height);
  padding: 4px 0;
  position: fixed;
}

@media (width <= 767px) {
  .header {
    max-height: var(--mobile-header-height);
    display: none;
  }
}

.header-container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  flex-shrink: 0;
  margin-right: 60px;
  padding: 8px 32px 8px 0;
}

.header-logo svg {
  width: 100%;
  max-width: 174px;
}

.header-nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  display: flex;
}

.header-menu {
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-link {
  color: var(--color-grey-10);
  padding: 16px;
  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
  transition: color .3s, background-color .3s;
  position: relative;
}

.header-link:after {
  content: "";
  background-color: #0000;
  height: 2px;
  transition: background-color .3s;
  position: absolute;
  bottom: 10px;
  left: 16px;
  right: 16px;
}

.header-link:hover:after {
  background-color: var(--color-primary-dark70);
}

.header-link:focus:after, .header-link--selected:after {
  background-color: var(--color-primary-light100);
}

.header-language-container {
  padding: 12px;
}

.header-call-button {
  margin-left: 60px;
}

.hero {
  background-color: var(--color-grey-100);
  padding-top: var(--min-header-height);
  min-height: 100vh;
  padding: 160px 0;
}

@media (width <= 767px) {
  .hero {
    padding-top: var(--mobile-header-height);
    padding: 56px 0;
  }
}

@media screen and (height >= 1200px) {
  .hero {
    min-height: fit-content;
  }
}

.hero-container {
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  display: grid;
}

@media (width <= 767px) {
  .hero-container {
    flex-direction: column;
    display: flex;
  }
}

.hero-image {
  width: 60%;
  margin: auto;
}

@media (width <= 767px) {
  .hero-image {
    order: -1;
    width: 38%;
    margin: 12px auto;
  }
}

.hero-image img, .hero-image svg {
  width: 100%;
}

.hero-title {
  margin-bottom: 80px;
}

@media (width <= 767px) {
  .hero-title {
    order: -1;
    margin-bottom: 0;
  }
}

.hero-description {
  margin-bottom: 80px;
  line-height: 1.4;
}

@media (width <= 767px) {
  .hero-description {
    margin-bottom: 0;
    line-height: 1.5;
  }
}

.services {
  background-color: var(--color-grey-90);
  padding: 160px 0 0;
}

@media (width <= 767px) {
  .services {
    padding-top: 56px;
  }
}

.services-title {
  width: 50%;
  margin-bottom: 80px;
}

@media (width <= 767px) {
  .services-title {
    width: 80%;
    margin-bottom: 32px;
  }
}

.services-link {
  background-color: var(--color-grey-80);
  height: 100%;
  padding: 32px 32px 76px;
  transition: background-color .3s;
  display: block;
  position: relative;
}

@media (width <= 767px) {
  .services-link {
    padding-bottom: 32px;
  }
}

.services-link:after {
  content: "";
  background-image: url("arrow.486f492b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 38px;
  height: 16px;
  position: absolute;
  bottom: 32px;
  left: 32px;
}

@media (width <= 767px) {
  .services-link:after {
    content: none;
  }
}

.services-link:hover {
  background-color: var(--color-primary-dark100);
  transition: background-color .3s;
}

.services-link:focus {
  background-color: var(--color-primary-dark80);
}

.video {
  background-color: var(--color-grey-90);
  padding: 140px 0 160px;
}

@media (width <= 767px) {
  .video {
    padding-top: 16px;
    padding-bottom: 56px;
  }
}

.video-title {
  text-align: center;
  margin: 0 auto 80px;
}

@media (width <= 767px) {
  .video-title {
    margin-bottom: 32px;
  }
}

.video-container {
  position: relative;
}

.video-container:before {
  content: "";
  background-color: var(--color-primary-dark70);
  clip-path: polygon(0 0, 92% 0, 100% 13%, 100% 100%, 0 100%);
  z-index: 1;
  width: calc(82% + 1px);
  margin: auto;
  position: absolute;
  inset: -1px;
}

@media (width <= 767px) {
  .video-container:before {
    width: calc(100% + 1px);
  }
}

.video-player {
  background-color: var(--color-grey-90);
  clip-path: polygon(0 0, 92% 0, 100% 13%, 100% 100%, 0 100%);
  z-index: 1;
  width: 82%;
  margin: 0 auto;
  padding: 84px 132px;
  position: relative;
}

@media (width <= 767px) {
  .video-player {
    width: 100%;
  }
}

.video-iframe-wrapper {
  width: 100%;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
}

.video-iframe {
  border: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.projects {
  background-color: var(--color-grey-100);
  padding-top: 160px;
}

@media (width <= 767px) {
  .projects {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

.projects-title {
  margin-bottom: 24px;
}

.projects-info {
  width: 50%;
  margin-bottom: 80px;
}

@media (width <= 767px) {
  .projects-info {
    width: 100%;
    margin-bottom: 32px;
  }
}

.projects-card {
  position: relative;
  overflow: hidden;
}

.projects-link {
  z-index: 1;
  align-items: flex-end;
  width: 100%;
  min-height: 280px;
  display: flex;
  position: relative;
}

.projects-link:before {
  content: "";
  background-color: var(--color-primary-dark100);
  opacity: 0;
  z-index: 1;
  transition: opacity .3s;
  position: absolute;
  inset: 0;
}

.projects-link:hover:before {
  opacity: .2;
}

.projects-link:focus:before {
  background-color: var(--color-primary-dark80);
  opacity: .2;
}

.projects-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.projects-text {
  z-index: 2;
  justify-content: flex-start;
  width: 100%;
  padding: 32px 32px 12px;
  font-size: 24px;
  display: flex;
  position: relative;
}

.about {
  background-color: var(--color-grey-100);
  padding-top: 130px;
  padding-bottom: 160px;
}

@media (width <= 767px) {
  .about {
    padding-top: 32px;
    padding-bottom: 56px;
  }
}

.about-inner-container {
  background-color: var(--color-grey-85);
  padding: 80px 100px;
}

@media (width <= 767px) {
  .about-inner-container {
    background-color: #0000;
    padding: 0;
  }
}

.about-title {
  width: 50%;
  margin-bottom: 48px;
}

@media (width <= 767px) {
  .about-title {
    width: 100%;
    margin-bottom: 32px;
  }

  .about-picture {
    margin-bottom: 32px;
  }
}

.about-description {
  width: 50%;
  margin-bottom: 48px;
}

@media (width <= 767px) {
  .about-description {
    width: 100%;
    margin-bottom: 32px;
  }
}

.values {
  background-color: var(--color-grey-90);
  padding: 160px 0;
}

@media (width <= 767px) {
  .values {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

.values-title {
  margin-bottom: 80px;
}

@media (width <= 767px) {
  .values-title {
    margin-bottom: 32px;
  }
}

.values-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  display: grid;
}

@media (width <= 767px) {
  .values-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}

.values-item {
  background-color: var(--color-grey-80);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 28px;
  padding: 32px;
  display: flex;
}

@media (width <= 767px) {
  .values-item {
    padding: 40px 12px;
  }
}

.values-item:hover, .values-item:focus {
  clip-path: polygon(0 0, 92% 0, 100% 13%, 100% 100%, 0 100%);
}

.values-icon {
  justify-content: center;
  align-items: center;
  width: 58px;
  height: 58px;
  display: flex;
}

@media (width <= 768px) {
  .values-icon {
    width: 32px;
    height: 32px;
  }
}

.values-icon svg {
  max-width: 100%;
  max-height: 100%;
}

.values-text {
  text-transform: uppercase;
  font-family: var(--font-secondary);
  letter-spacing: .14px;
  color: var(--color-grey-10);
  text-align: center;
  letter-spacing: 2px;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
}

@media (width <= 767px) {
  .values-text {
    letter-spacing: .08px;
    font-size: 16px;
  }
}

.info {
  background-color: var(--color-grey-100);
  padding: 160px 0;
  position: relative;
}

@media (width <= 767px) {
  .info {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

.info-title {
  text-transform: none;
  text-align: center;
  margin-bottom: 40px;
}

@media (width <= 767px) {
  .info-title {
    text-align: left;
    margin-bottom: 32px;
  }
}

.info-description {
  text-align: left;
  width: 66%;
  margin: 0 auto;
}

.info-expand-button {
  display: none;
}

.info-bottom-pic {
  margin: 60px auto 0;
}

@media (width <= 767px) {
  .info-description {
    --max-lines: 8;
    --line-height: 1.5;
    text-align: left;
    width: 100%;
    max-height: calc(var(--max-lines) * 1em * var(--line-height));
    line-height: var(--line-height);
    overflow: hidden;
  }

  .info-expand-button {
    color: var(--color-primary-dark70);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    display: block;
  }

  .info-description:has( + .info-expand-button input:checked) {
    max-height: none;
  }

  .info-expand-button input:not(:checked) + .info-expand-text-on {
    display: block;
  }

  .info-expand-button input:not(:checked) + .info-expand-text-on + .info-expand-text-off, .info-expand-button input:checked + .info-expand-text-on {
    display: none;
  }

  .info-expand-button input:checked + .info-expand-text-on + .info-expand-text-off {
    display: block;
  }

  .info-bottom-pic {
    width: 20%;
    margin-top: 32px;
  }
}

.contact {
  background-color: var(--color-grey-100);
  padding-top: 120px;
  padding-bottom: 80px;
}

@media (width <= 767px) {
  .contact {
    background-color: var(--color-grey-90);
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

.contact-container {
  background-color: var(--color-grey-90);
  border: 1px solid var(--color-primary-dark70);
  grid-template-columns: 2fr 3fr;
  gap: 32px;
  padding: 80px 130px 80px 100px;
  display: grid;
}

@media (width <= 767px) {
  .contact-container {
    background-color: #0000;
    border-color: #0000;
    padding: 0;
    display: block;
  }
}

.contact-title {
  margin-bottom: 16px;
}

@media (width <= 767px) {
  .contact-title {
    margin-bottom: 32px;
    font-size: 40px;
  }

  .contact-info {
    margin-bottom: 32px;
    font-size: 20px;
  }
}

.form-group {
  flex-direction: column;
  margin-bottom: 12px;
  display: flex;
  position: relative;
}

.form-label {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  display: block;
}

.form-input {
  background-color: inherit;
  color: var(--color-grey-0);
  padding: 6px 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
}

.form-input:focus {
  border-bottom: 1px solid var(--color-system-blue);
}

.form-input-error {
  width: 100%;
  min-height: 48px;
  color: var(--color-system-error);
  margin-top: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  position: relative;
  bottom: 0;
}

.form-group--error .form-input {
  border-bottom: 1px solid var(--color-system-error);
}

.form-input-error-text {
  display: none;
}

.form-group--error .form-input-error-text {
  display: inline;
}

.form-group--checkbox {
  flex-direction: row;
  align-items: center;
  gap: 12px;
  display: flex;
}

.form-input, .form-select {
  border-bottom: 1px solid var(--color-grey-30);
}

.form-input::placeholder, .form-select::placeholder {
  border-bottom: 1px solid var(--color-grey-60);
}

.form-group--checkbox {
  margin-bottom: 56px;
  padding-left: 8px;
}

.form-checkbox-container {
  align-items: center;
  gap: 12px;
  display: flex;
}

.form-checkbox {
  border: 1px solid var(--color-grey-10);
  border-radius: 0%;
  width: 20px;
  height: 20px;
  position: relative;
}

.form-checkbox:checked:after {
  content: "";
  background-image: url("check.f410c338.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  transition: opacity .2s ease-in-out;
  display: block;
  position: absolute;
}

.form-checkbox-label {
  margin: 0;
  padding: 0;
}

.form-checkbox-link, .form-checkbox-link:hover {
  text-decoration: underline;
}

.form-input {
  transition: background-color 5000s ease-in-out, color 5000s ease-in-out;
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-input:-webkit-autofill {
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-input:-webkit-autofill:hover {
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-input:-webkit-autofill:focus {
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-input:-moz-autofill {
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-input:-ms-input-placeholder {
  background-color: var(--color-grey-90) !important;
  color: var(--color-grey-10) !important;
}

.form-select-container {
  margin-bottom: 48px;
}

.form-select-label {
  margin-bottom: 16px;
}

.select-hidden {
  visibility: hidden;
  padding-right: 10px;
  display: none;
}

.select {
  cursor: pointer;
  width: 100%;
  height: 40px;
  font-size: 16px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.select-styled {
  background-color: var(--color-grey-90);
  border-bottom: 1px solid var(--color-grey-30);
  font-family: var(--font-primary);
  padding: 12px 16px;
  transition: all .2s ease-in;
  position: absolute;
  inset: 0;
}

.form-group--error .select-styled {
  border-bottom: 1px solid var(--color-system-error);
}

.select-styled:after {
  content: "";
  border-style: solid;
  border-width: 0 2px 2px 0;
  border-color: var(--color-primary-light100);
  width: 10px;
  height: 10px;
  position: absolute;
  top: 14px;
  right: 26px;
  transform: rotate(45deg);
}

.select-styled:hover, .select-styled:active, .select-styled.active {
  background-color: var(--color-grey-90);
}

.select-styled:active:after, .select-styled.active:after {
  border-width: 2px 0 0 2px;
}

.select-options {
  z-index: 999;
  background-color: var(--color-grey-80);
  margin: 0;
  list-style: none;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
}

.select-options li {
  text-indent: 15px;
  margin: 0;
  padding: 10px 2px;
  transition: all .15s ease-in;
}

.select-options li:hover {
